import React, { Component } from 'react';
import './get_started_component.css';

class GetStarted extends Component {

  render() {

    return (

        <div className = "_align container">
              <div className = "_started" >Get started for free - join thousands of students and researchers 
              already using Ignitus to share their knowledge, work together, and get amazing opportunites.</div>
              <a href="https://bit.ly/2NwWLhp" className="btn btn-md _button" role="button">Sign Up</a>
         </div>
        
    )
  }
}

export default GetStarted;


